import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Role } from '@app/enum/app_enum';
import { AuthenticationService } from "@core/authentication/authentication.service";
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Component({
  selector: 'app-pagenotfound',
  templateUrl: './pagenotfound.component.html',
  styleUrls: ['./pagenotfound.component.css']
})
export class PagenotfoundComponent implements OnInit {
  state$: Observable<object>;
  public errorMessage: any;

  constructor(private _router: Router, private _auth: AuthenticationService, public activatedRoute: ActivatedRoute) { }

  ngOnInit(): void {
    this.errorMessage = JSON.parse(localStorage.getItem('errorMessage'));
  
  }

  goToHomepage() {
    let data = this._auth.currentUserValue;
    if (data) {
      if (data.role == Role.Student)
        this._router.navigateByUrl(`${data.role.toLowerCase()}/classes/management`);
      else this._router.navigateByUrl(`${data.role.toLowerCase()}/home/${data.role.toLowerCase()}-home`);
    } else this._router.navigateByUrl('/login');

    localStorage.removeItem('errorMessage');
  }

}
