import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { map } from 'rxjs/operators';

import { environment } from "@env/environment";
import { Class } from '@app/shared/models/class';

@Injectable({
  providedIn: 'root'
})
export class ClassService {
  private baseUrl = environment.apiUrl;

  constructor(private http: HttpClient) { }

  classLists(search: String, direction: String, page: any) {
    let url: any;
    if (search)
      url = this.baseUrl + "/classes?search=" + search;
    else if (direction)
      url = this.baseUrl + "/classes?direction=" + direction + "&page=" + page;
    else url = this.baseUrl + "/classes?page=" + page;

    return this.http.get<any>(url).pipe(map((obj) => obj || {}));
  }

  classDetail(id: String) {
    let url = this.baseUrl + "/classes/" + id;
    return this.http.get<any>(url).pipe(map((obj) => obj || []));
  }

  createHomework(classId: String, homework: String) {
    let url = this.baseUrl + "/classes/" + classId + "/homework";
    let data = {
      homework: homework
    };
    return this.http.post<any>(url, data).pipe(map((obj) => obj || []));
  }

  createHomeworkF(classId: String, file: any) {

    let url = this.baseUrl + "/classes/" + classId + "/homework";
    const formData = new FormData();
    formData.append("homeWorkImage", file);
    // let data = {
    //   homeWorkImage: file
    // };
    return this.http.post<any>(url, formData).pipe(map((obj) => obj || []));
  }

  onAttandance(obj: any, userId: String, classId: String) {
    let url = this.baseUrl + "/classes/" + classId + "/" + userId + "/attendance";
    return this.http.post<any>(url, obj).pipe(map((obj) => obj || []));
  }

  createReply(classId: String, homeworkId: String, reply: String) {
    let url = this.baseUrl + "/classes/" + classId + "/homework/" + homeworkId + "/reply";
    let data = {
      reply: reply
    };
    return this.http.post<any>(url, data).pipe(map((obj) => obj || []));
  }
  createReplyF(classId: String, homeworkId: String, file: any) {
    let url = this.baseUrl + "/classes/" + classId + "/homework/" + homeworkId + "/reply";
    const formData = new FormData();
    formData.append("file", file);
    // let data = {
    //   reply: reply
    // };
    return this.http.post<any>(url, formData).pipe(map((obj) => obj || []));
  }
  classCreate(data: Class) {
    let url = this.baseUrl + "/classes";
    return this.http.post<any>(url, data).pipe(map((obj) => obj || []));
  }

  classUpdate(data: Class) {
    let url = this.baseUrl + "/classes/" + data._id;
    return this.http.put<any>(url, data).pipe(map((obj) => obj || []));
  }

  deleteClass(id: String) {
    let url = this.baseUrl + "/classes/" + id;

    return this.http
      .delete<any>(url)
      .pipe(map((obj) => obj || {}));
  }

  getClassFeedback(id: String) {
    let url = this.baseUrl + "/classes/" + id + "/feedback";
    return this.http.get<any>(url).pipe(map((obj) => obj || {}));
  }

  createFeedback(classId: String, userId: String, feedBackId: String, feedBack: String) {
    let url = this.baseUrl + "/classes/" + classId + "/feedback/" + feedBackId + '/chat';
    let data = {
      "chat": feedBack,
      "toUserId": userId
    };
    return this.http.post<any>(url, data).pipe(map((obj) => obj || []));
  }

  getFeedbackChat(classId: String, feedBack: String) {
    let url = this.baseUrl + "/classes/" + classId + "/feedback/" + feedBack + "/chat";
    return this.http.get<any>(url).pipe(map((obj) => obj || {}));
  }

  getTimeLine(classId: String) {
    let url;
    if (classId)
      url = this.baseUrl + "/classes/timeline?classId=" + classId;
    else
      url = this.baseUrl + "/classes/timeline";
    return this.http.get<any>(url).pipe(map((obj) => obj || []));
  }

  onCallFaceTime(classId: String, userType: String, name: String) {
    let url = this.baseUrl + "/classes/" + classId + "/meeting?room=" + classId + "&userType=" + userType + "&username=" + name;
    window.open(url, "_blank");
  }

}
